const firebase = require("firebase/app");
require('firebase/firestore');
const stuff = require("../stuff");

const firebaseConfig = {
    apiKey: stuff.apiKey,
    authDomain: stuff.authDomain,
    databaseURL: stuff.databaseURL,
    projectId: stuff.projectId,
    storageBucket: stuff.storageBucket,
    messagingSenderId: stuff.messagingSenderId,
    appId: stuff.appId,
    measurementId: stuff.measurementId
  };
// Initialize Firebase
firebase.initializeApp(firebaseConfig);
//firebase.analytics();
const db = firebase.firestore();

module.exports = {db: db};